.NavigationBar {
  min-width: 10%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  background-image: -webkit-gradient(linear, left bottom, left top, from(rgb(7, 150, 207)), to(rgb(10, 29, 58)));
  background-image: -webkit-linear-gradient(bottom, rgb(7, 150, 207), rgb(10, 29, 58));
  background-image: -o-linear-gradient(bottom, rgb(7, 150, 207), rgb(10, 29, 58));
  background-image: linear-gradient(to top, rgb(7, 150, 207), rgb(10, 29, 58));
  color: white;
}

.NavigationBar-section {
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.NavigationBar-Button {
  font-family: Roboto;
  font-size: 0.9rem;
  line-height: 2.71;
  text-align: left;
  color: white;
  background-color: Transparent;
  border: none;
}

.NavigationBar-Button:focus {
  outline: 0;
}

.NavigationBar-Heading {
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 1.2px;
  text-align: left;
  color: #8992ca;
}


